
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { share, wxPay } from "@/utils/wx_sdk.js";
export default {
name: "shop",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading
},
data() {
return {
isLoading: true,
storeInfo: {
total_sales: 0,
date_sales: 0,
total_revenue: 0,
date_revenue: 0,
total_fan: "0",
date_fan: 0,
date_order: 0,
date_visitor: 0,
rebate_amout: "0.00",
member_name: "鞋品荟第110店铺",
member_avatar:
"http://img.xiepinhui.com.cn/sys/default/user/avatar.jpg",






































}
};
},
created() {
this.getThelordstore();
},
methods: {
async getThelordstore() {
let data = {
plat: 3,
account: this.account,
token: this.token
};
const [err, res] = await api.thelordstore(data);
if (err) {
this.$vux.toast.text(err.msg);
this.isLoading = false;
return;
}
if (res && res.code == "2000") {
this.storeInfo = res.data;
}
this.isLoading = false;
},
manageClick(e) {
let type = e.currentTarget.dataset.type;
let isopen = e.currentTarget.dataset.isopen;

var clickType=type;
var _this=this;
var clickUrl='';

if (!isopen) {
this.$vux.toast.text("暂无权限");
return;
}
switch (clickType) {
case "1":
clickUrl ="offlineStore";
break;
case "5":
clickUrl = "fanManagement";
break;
case "3":
clickUrl =
"inventoryManage?type=" +
clickType;
break;
case "4":
let _storeInfo = {
shop_name: this.storeInfo.member_name,
real_name: this.storeInfo.member_truename,
sub_member_id: this.storeInfo.member_id,
tel: this.storeInfo.member_mobile,
member_avatar: this.storeInfo.member_avatar
};
this.update_storeInfo(_storeInfo);
clickUrl = "shopSetting?sub_member_id="+this.storeInfo.member_id;
break;
case "2":
clickUrl = "subStoreList";
break;
case "6":
clickUrl = "offlineStoreOrder";
break;
case "7":
clickUrl = "subfanManagement";
break;
case "8":
clickUrl = "centerFull/myshop/replenishCar/replenishCar";
break;
case "9":
clickUrl = "centerFull/myshop/replenishmentOrder/replenishmentOrder";
break;
case "10":
clickUrl = "subinventoryManage"; 
break;
case "11":
clickUrl = "subreplenishCar";
break;
case "12":
clickUrl = "subreplenishOrder";
break;
case "13":
clickUrl = "rechargeMoneyOffline";
break;
}
_this.$router.push(clickUrl);
},
...mapActions(["update_storeInfo"])
},
filters: {

money_fil(val) {
if (!val) {
return;
}
val = Number(val);
if (val >= 10000) {
return val / 10000 + "万";
}
return val;
}
},
computed: {
...mapGetters(["user", "account", "token"])
}
};



